'use client'
import { useEffect, useState } from 'react'
import { useForm } from 'react-hook-form'
import { Card, CardActions, CardContent, Container, Grid, IconButton, Typography, Dialog, DialogTitle, DialogContent, DialogActions, TextField, Button, Snackbar, Alert } from '@mui/material'
import EditIcon from '@mui/icons-material/Edit'
import DeleteIcon from '@mui/icons-material/Delete'
import { fetchServices, createService, deleteServiceById, updateServiceById } from '@/services/api'
import { useAuth } from '@/context/AuthContext'
import { motion } from 'framer-motion'
import AdminBreadcrumbs from '@/components/AdminBreadcrumbs'

export default function AdminServicesPage() {
  const { token } = useAuth()
  const [services, setServices] = useState<any[]>([])
  const [editOpen, setEditOpen] = useState(false)
  const [editing, setEditing] = useState<{ _id: string; title: string; price: number; duration: number; description?: string } | null>(null)
  const [toast, setToast] = useState<{open: boolean; msg: string; sev: 'success'|'error'}>({open:false,msg:'',sev:'success'})
  const [successMessage, setSuccessMessage] = useState(false)

  // Button style
  const btnStyle: React.CSSProperties = {
    background: '#2563eb',
    color: '#fff',
    border: 'none',
    borderRadius: 6,
    padding: '10px 24px',
    fontWeight: 700,
    fontSize: 16,
    marginTop: 12,
    cursor: 'pointer',
    transition: 'background 0.2s',
  }

  const { register, handleSubmit, reset, formState: { errors } } = useForm<{ title: string; price: number; duration: number; description?: string }>()

  // Input style helper
  function inputStyle(error?: any): React.CSSProperties {
    return {
      width: '100%',
      padding: '10px 12px',
      marginBottom: 10,
      border: error ? '2px solid #ef4444' : '1px solid #d1d5db',
      borderRadius: 6,
      fontSize: 16,
      outline: 'none',
      boxSizing: 'border-box',
      background: '#fff',
      transition: 'border 0.2s',
    }
  }

  const loadServices = async () => {
    try {
      const data = await fetchServices()
      setServices(data)
    } catch (err) {
      console.error('Fehler beim Laden der Services:', err)
    }
  }

  useEffect(() => {
    loadServices()
  }, [])

  const handleDeleteService = async (id: string) => {
    try {
      await deleteServiceById(id, token!)
      loadServices()
    } catch (err) {
      console.error('Fehler beim Löschen:', err)
    }
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <AdminBreadcrumbs items={[
        { label: 'Mein Salon', href: '/admin' },
        { label: 'Services' },
      ]} />
      <Typography variant="h4" fontWeight={800} gutterBottom>
        Services verwalten
      </Typography>

      {/* Service anlegen */}
      <form
        onSubmit={handleSubmit(async (data) => {
          try {
            await createService(
              {
                title: data.title.trim(),
                description: data.description?.trim(),
                price: Number(data.price),
                duration: Number(data.duration),
              },
              token!
            )
            setSuccessMessage(true)
            await loadServices()
            setTimeout(() => setSuccessMessage(false), 2000)
            reset()
          } catch (err) {
            console.error('Fehler beim Erstellen des Services:', err)
          }
        })}
        style={{ marginBottom: '2rem' }}
      >
        <input
          type="text"
          placeholder="Titel (z. B. Herrenpaket)"
          {...register('title', { required: 'Titel ist erforderlich' })}
          style={inputStyle(errors.title)}
        />
        {errors.title && <p style={{ color: '#ef4444' }}>{errors.title.message}</p>}

        <input
          type="number"
          placeholder="Preis in €"
          step="0.01"
          {...register('price', { required: 'Preis ist erforderlich', min: { value: 0, message: '>= 0' } })}
          style={inputStyle(errors.price)}
        />
        {errors.price && <p style={{ color: '#ef4444' }}>{errors.price.message}</p>}

        <input
          type="number"
          placeholder="Dauer in Minuten"
          {...register('duration', { required: 'Dauer ist erforderlich', min: { value: 5, message: 'mind. 5 Min' } })}
          style={inputStyle(errors.duration)}
        />
        {errors.duration && <p style={{ color: '#ef4444' }}>{errors.duration.message}</p>}

        <textarea
          placeholder="Beschreibung (optional)"
          {...register('description')}
          style={{ ...inputStyle(), minHeight: 80 }}
        />

        <button type="submit" style={btnStyle}>Hinzufügen</button>
        {successMessage && (
          <p style={{ color: '#22c55e', fontWeight: 600, marginTop: 8 }}>
            Service erfolgreich hinzugefügt!
          </p>
        )}
      </form>

      {/* Service-Liste */}
      <Grid container spacing={2} sx={{ mb: 3 }}>
        {services.map(s => (
          <Grid
            key={s._id}
            sx={{
              width: { xs: '100%', sm: '50%', md: '33.33%' },
              display: 'flex'
            }}
          >
            <Card sx={{ borderRadius: 3, width: '100%', maxWidth: 420 }}>
              <CardContent sx={{ py: 1.5 }}>
                <Typography variant="subtitle1" sx={{ fontWeight: 700 }}>
                  {s.title}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Dauer: {'duration' in s ? (s as any).duration ?? '—' : '—'} Min.
                </Typography>
              </CardContent>
              <CardActions sx={{ justifyContent: 'flex-end', py: 1 }}>
                <IconButton
                  aria-label="Bearbeiten"
                  onClick={() => {
                    setEditing({ _id: s._id, title: s.title, price: s.price, duration: s.duration, description: s.description });
                    setEditOpen(true);
                  }}
                >
                  <EditIcon />
                </IconButton>
                <IconButton
                  aria-label="Löschen"
                  color="error"
                  onClick={() => handleDeleteService(s._id)}
                >
                  <DeleteIcon />
                </IconButton>
              </CardActions>
            </Card>
          </Grid>
        ))}

        {services.length === 0 && (
          <Grid sx={{ width: '100%' }}>
            <Typography color="text.secondary">Noch keine Services angelegt.</Typography>
          </Grid>
        )}
      </Grid>

      {/* Service bearbeiten Dialog */}
      <Dialog open={editOpen} onClose={() => setEditOpen(false)} fullWidth maxWidth="xs">
        <DialogTitle>Service bearbeiten</DialogTitle>
        <DialogContent sx={{ display:'flex', flexDirection:'column', gap:2, pt:2 }}>
          <TextField
            label="Titel"
            value={editing?.title ?? ''}
            onChange={e => setEditing(prev => prev ? ({ ...prev, title: e.target.value }) : prev)}
          />
          <TextField
            label="Preis (€)"
            type="number"
            value={editing?.price ?? ''}
            onChange={e => setEditing(prev => prev ? ({ ...prev, price: Number(e.target.value) }) : prev)}
          />
          <TextField
            label="Dauer (Minuten)"
            type="number"
            value={editing?.duration ?? ''}
            onChange={e => setEditing(prev => prev ? ({ ...prev, duration: Number(e.target.value) }) : prev)}
          />
          <TextField
            label="Beschreibung"
            multiline
            minRows={2}
            value={editing?.description ?? ''}
            onChange={e => setEditing(prev => prev ? ({ ...prev, description: e.target.value }) : prev)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setEditOpen(false)}>Abbrechen</Button>
          <Button
            variant="contained"
            onClick={async () => {
              if (!editing) return
              try {
                const updated = await updateServiceById(editing._id, {
                  title: editing.title,
                  duration: editing.duration
                }, token!)
                setToast({open:true, msg:'Service gespeichert', sev:'success'})
                setEditOpen(false)
                // Liste aktualisieren
                setServices(prev => prev.map(s => s._id === updated._id ? updated : s))
              } catch (e) {
                setToast({open:true, msg:'Fehler beim Speichern', sev:'error'})
              }
            }}
          >
            Speichern
          </Button>
        </DialogActions>
      </Dialog>

      {/* Snackbar für Feedback */}
      <Snackbar
        open={toast.open}
        autoHideDuration={2000}
        onClose={() => setToast(p => ({...p, open:false}))}
        anchorOrigin={{ vertical:'bottom', horizontal:'center' }}
      >
        <Alert onClose={() => setToast(p => ({...p, open:false}))} severity={toast.sev} variant="filled">
          {toast.msg}
        </Alert>
      </Snackbar>
    </Container>
  )
}